import React, { memo } from 'react';
import { useStoreon } from 'storeon/react';
import WishPageLayoutContainer from './WishPageLayout/WishPageLayoutContainer';

const WishPage = ({
    context
}) => {
    const { wishListStore, recommendedProductsStore ,currency} = useStoreon('currency','wishListStore', 'recommendedProductsStore');
    const {
        breadcrumbs,
        profile,
    } = context;

    const wishlist = profile?.wishlist;

    return (
        <WishPageLayoutContainer
            recomendetProducts={recommendedProductsStore}
            breadcrumbs={breadcrumbs}
            list_wishes={wishListStore}
            isLoading={wishListStore?.isLoadingListMyWish}
            isLoadingMyWish={wishListStore?.isLoadingMyWish}
            wishlist={wishlist}
            currency={currency}
        />
    )
}

export default (WishPage);