import React, { memo } from 'react';
import Container from '../../../Views/GridContainerBlock/Container';
import MyWishList from './DetailsPage/MyWishList';


const WishPageLayout = ({
    recomendetProducts,
    isLoadingMyWish,
    breadcrumbs,
    list_wishes,
    isLoading,
    currency,
    wishlist,
    role,

    changePaginationsWishList,
}) => {

   
    return (
        <Container>            
            <MyWishList
                changePaginationsWishList={changePaginationsWishList}
                recomendetProducts={recomendetProducts}
                isLoadingMyWish={isLoadingMyWish}
                breadcrumbs={breadcrumbs}
                list_wishes={list_wishes}
                isLoading={isLoading}
                currency={currency}
                role={role}
            />
        </Container>
    )
}

export default (WishPageLayout);