import React, { memo } from 'react';
import AsyncComponent from '../../../../helpers/asyncComponent';
import Text from '../../../../helpers/Text';
import Pagination from '../../../../Views/Pagination';
import Title from '../../../../Views/Title';
import CountProduct from './CountProduct';
import LayoutProduct from './LayoutProduct';
import BlockSpinner from '../../../../Views/SpinnerWrapper';
import DefaultWishPagePreview from '../DefaultWishPagePreview';
import Breadcrumbs from '../../../../Views/Breadcrumbs';
import BlockGrid from '../../../../Views/GridContainerBlock';
import Row from '../../../../Views/GridContainerBlock/Row/Row';
import Coll from '../../../../Views/GridContainerBlock/Coll';


const AsyncProductCard = AsyncComponent(() => {
    return import('../../../../Views/ProductCard/ProductCardContainer');
})
const AsynNoProducts = AsyncComponent(() => {
    return import('../../../../Views/ProductDetailsViews/InfoBlockProducts');
});

const MyWishList = ({
    isLoadingMyWish,
    list_wishes,
    isLoading,
    currency,
    breadcrumbs,
    recomendetProducts,
    changePaginationsWishList,
    role,
}) => {

    return (
        <React.Fragment>
            {
                !!list_wishes.results?.length ?
                    <Row>
                        <Breadcrumbs breadcrumbs={breadcrumbs} />
                        <Coll className="cart__empty">

                            <Title variant={'wishlist'} type={'h1'}>
                                <Text text={'favorits.product'} />
                            </Title>
                            <CountProduct>
                                <span>{list_wishes.count}</span>
                                {
                                    Text({ text: 'goods' })
                                }
                            </CountProduct>
                        </Coll>
                    </Row>
                    : <DefaultWishPagePreview
                        recomendetProducts={recomendetProducts}
                        breadcrumbs={breadcrumbs}
                        currency={currency}
                        role={role}
                    />

            }
            {
                isLoading ?
                    <LayoutProduct>
                        {
                            list_wishes.results.map((el) => {
                                const data = el.product ? el.product : el;
                                return (
                                    <AsyncProductCard
                                        {...data}
                                        url={data.url}
                                        key={data.id}
                                        title={data.title}
                                        id={data.id}
                                        brand={data.brand}
                                        is_liked={data.is_liked}
                                        currency={currency}
                                        prices={data.prices}
                                        stock={data.stock}
                                        colors={data.colors}
                                        images={data.images}
                                        isSales={data.is_closeout}
                                        isNew={data.is_new}
                                        isHit={data.is_bestseller}
                                        sizes={data.sizes}
                                        product_rc={data.product_rc}
                                        profile={data.profile}
                                        isLoadingMyWish={isLoadingMyWish}
                                    />
                                );
                            })

                            //     <BlockSpinner.SpinnerWrapper>
                            //     <BlockSpinner.Spinner />
                            // </BlockSpinner.SpinnerWrapper>
                        }
                    </LayoutProduct>
                    : <AsynNoProducts>
                    </AsynNoProducts>
            }
            <Pagination
                allCount={list_wishes.count}
                count={30}
                handlerChangePaginations={changePaginationsWishList}
            />

        </React.Fragment>
    )
}

export default (MyWishList);